.img-card{
    height: 168px;
    width: 125px;
    min-width: 125px;
    margin: 3em 2em;
 
}
img{
    max-width:100%;
    min-width: 100%;
    object-fit: cover;
    height: 100%;
    border-radius: 10px;
}
.card-info{
    display: flex;
    flex-direction:column ;
    margin: 0;
}
.location{
    align-items: center;
    display: flex;
}
.location p{
    text-transform: uppercase;
    letter-spacing: 4px;
    font-weight: 500;
    font-size: 14px;
    margin: 0 0.5em;
}
.location>a{
    color: rgba(145, 142, 155, 1)
    ;
    font-size: 12px;
}
.top{
    display: flex;
    
}

.txt{
    margin: 3em 0em;
}
.date{
    font-weight: 700;
    font-size: 14px;
    margin: 0.5em 0;
    color: rgba(43, 40, 58, 1)
}

.desc{
    font-size: 13px;
    margin-right: 3em;
}