*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.main-container{
  background-color: lightgray;
  padding: 2em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card{
  height: auto;
  border-radius: 10px;
  background-color: white;
  width: 550px;
}


hr {
  border: 0;
  height: 1px;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
}
